<template>
  <div id="mobileUserEdit">
    <Modal
      v-model="redLineRebuild"
      title="编辑红线斑块信息">
      <Form ref="edit" :model="EditData" :rules="EditRule" :label-width="150">
        <FormItem prop="id" label="ID：" style="display: none">
          {{EditData.id}}
        </FormItem>
        <FormItem prop="srIdBumber" label="红线编码：">
          <Input v-model.trim="EditData.srIdBumber" size="default"/>
        </FormItem>
        <FormItem prop="type" label="类型：">
          <Select v-model="EditData.type" size="default" @on-change="changetarget">
            <Option value="01">生态功能重要区</Option>
            <Option value="02">生态环境敏感区</Option>
          </Select>
        </FormItem>
        <FormItem prop="target" label="生态系统服务功能与保护目标：">
          <Select v-model="EditData.target" size="default">
            <Option v-for="item in targetList" :value="item.value" :key="item.value">{{ item.label }}</Option>
          </Select>
        </FormItem>
        <FormItem prop="area" label="区域面积(平方千米)：">
          <Input v-model.trim="EditData.area" size="default" disabled/>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" @click="editSubmit">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import { updateRedline } from '@/api/acquisitionTable'
export default {
  name: 'redLine_edit',
  data () {
    return {
      EditRule: {},
      /* 编辑框 */
      redLineRebuild: false,
      /* 编辑数据 */
      EditData: {},
      targetList: [],
      targetList1: [
        {
          label: '水源涵养',
          value: '01'
        },
        {
          label: '生物多样性维护',
          value: '02'
        },
        {
          label: '水土保持',
          value: '03'
        },
        {
          label: '防风固沙',
          value: '04'
        },
        {
          label: '其他生态功能',
          value: '05'
        }
      ],
      targetList2: [
        {
          label: '水土流失',
          value: '01'
        },
        {
          label: '土地沙化',
          value: '02'
        },
        {
          label: '石漠化',
          value: '03'
        },
        {
          label: '其他敏感性',
          value: '04'
        }
      ]
    }
  },
  props: {
    /* 编辑行原数据 */
    redLineInfoData: {}
  },
  watch: {
    redLineInfoData () {
      this.dataHandle()
    }
  },
  mounted () {
  },
  methods: {
    /* 编辑框默认值数据处理 */
    dataHandle () {
      this.EditData.id = this.redLineInfoData.srldId
      this.EditData.srIdBumber = this.redLineInfoData.srldNumber
      this.EditData.area = this.redLineInfoData.srldArea
      this.EditData.name = this.redLineInfoData.srldName
      this.EditData.type = this.redLineInfoData.srldType
      if (this.redLineInfoData.srldType === '01') {
        this.targetList = this.targetList1
      } else if (this.redLineInfoData.srldType === '02') {
        this.targetList = this.targetList2
      }
      this.EditData.target = this.redLineInfoData.target
      this.EditData.plant = this.redLineInfoData.srldPlantType
      this.EditData.active = this.redLineInfoData.srldActive
      this.EditData.problem = this.redLineInfoData.srldProblem
      this.EditData.control = this.redLineInfoData.srldControl
    },
    changetarget (val) {
      if (val === '01') {
        this.targetList = this.targetList1
      } else if (val === '02') {
        this.targetList = this.targetList2
      }
    },
    /* 编辑移动端用户 */
    editSubmit () {
      this.$refs.edit.validate(valid => {
        if (valid) {
          updateRedline({
            data: {
              id: this.EditData.id,
              srIdBumber: this.EditData.srIdBumber,
              type: this.EditData.type,
              target: this.EditData.target
            }
          }).then(res => {
            if (res.data.code === '0000') {
              this.$Notice.success({ title: '编辑红线斑块信息成功' })
              this.$emit('getTableData')
              this.redLineRebuild = false
              this.EditData = {}
              this.reset()
            } else {
              this.$Notice.error({ title: res.data.msg })
            }
          })
        }
      })
    },
    // 重置表單參數
    reset () {
      this.EditData = {
        id: '',
        srIdBumber: '',
        area: '',
        name: '',
        type: '',
        target: '',
        plant: '',
        active: '',
        problem: '',
        control: ''
      }
      this.$refs.edit.resetFields()
    }
  }
}
</script>

<style scoped lang="scss">
</style>
